.Navbar{
    justify-content: space-between;
    display: flex;
    width: 100%;
    z-index: 1000;
    background-color: #003566;
    position: fixed;
    align-items: center;
    height: 64px;
}

.Navbar img{
    height: 50px;
    padding-left: 20px;
    margin: 10px;
}

.Navbar nav ul a {
    margin: 10px;
}
