.About{
    padding: 70px 20px 0 20px;
}

.About img{
    box-shadow: #023d8a62 0px 5px 15px;
    height: 250px;
}

.About .aboutcon{
    margin-bottom: 100px;
    flex-direction: column;
    display: flex;
    align-items: center;
}

.About .aboutcon h1{
    margin-bottom: 40px;
    font-size: 50px;
    text-align: center;
}

.About .aboutcon .info h2{
    margin: 16px 0 0 20px;
    text-align: left;
}

.About .info{
    display: flex;
}